'use strict';

var L = window.L;  // explicitly bring into scope to not be evil
var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;
var Header = require('./header.jsx');
var Popup = require('./map/popup.jsx'); 
var PointLayer = require('./map/pointLayer.jsx');
var titleCase = require('to-title-case');
var CountryActions = require('../actions/countryActions');
var MapViewActions = require('../actions/mapViewActions');
var CountryStore = require('../stores/countryStore');
var ProjectsStore = require('../stores/projectsStore');


function projectFeature(country, projectId) {
  var locations = country.properties.locations.filter(function(location) {
    return location.properties.id === projectId;
  });
  return {
    'type': 'FeatureCollection',
    'features': locations,
  };
}


var Project = React.createClass({displayName: "Project",

  propTypes: {
    id: React.PropTypes.number,
    name: React.PropTypes.string,
    totalCommitments: React.PropTypes.number,
  },

  mixins: [
    Reflux.connect(ProjectsStore, 'projects'),
    Reflux.connect(CountryStore, 'countries'),
  ],

  componentWillMount: function() {
    // load country and project data
    CountryActions.loadCountry.triggerPromise(this.props.params.countryId)
      .then(this.countryDidLoad);
  },

  componentWillReceiveProps: function(nextProps) {
    if (nextProps.params.projectId !== this.props.params.projectId) {
      // user navigated from one country directly to another's url
      delete this.country;
      delete this.project;
      CountryActions.loadCountry.triggerPromise(nextProps.params.countryId)
        .then(this.countryDidLoad);
    }
  },

  countryDidLoad: function(rawCountry) {
    // for now, work with the raw country data.
    // we can't to countryStore.getCountry, because this callback runs before
    // countryStore.onLoadCountryCompleted.
    // we should maybe have another store for the current country...
    this.country = CountryStore.utils.countryToFeature(rawCountry);

    this.project = projectFeature(this.country, this.props.params.projectId);

    var points = this.country.properties.locations
      .filter(function(location) {
        return (location.properties.id === this.props.params.projectId);
      }, this)
      .map(function(location) {
        return location.geometry.coordinates.slice().reverse();  // reversed because our geojson is backward
      });

    MapViewActions.changeBounds(L.latLngBounds(points),
      {paddingTopLeft: [0, 51]});  // clear the header height
  },

  locationPin: function(featureData, latlng) {
    var size = 16;
    return L.marker(latlng, {
      icon: L.divIcon({
        className: 'project-location-marker',
        iconSize: [size, size],
        iconAnchor: [size/2, size/2]
      }),
      riseOnHover: true
    });
  },

  locationPopup: function(location) {
    var d = location.properties;
    return (
      React.createElement(Popup, null, 
        React.createElement(Popup.Header, null, 
          React.createElement("h3", {className: "panel-title"}, d.locationName)
        ), 
        React.createElement("div", {className: "panel-body location-summary"}, 
          React.createElement("p", null, 
            React.createElement("strong", null, titleCase(d.title))
          ), 
          React.createElement("p", null, 
            React.createElement("strong", null, "ID: "), " ", this.props.params.projectId
          ), 
          React.createElement("p", {className: "details"}, 
            React.createElement("span", null, 
              React.createElement("span", {className: "locations-count"}, this.project.features.length), " locations."
            )
          )
        )
      )
    );
  },

  render: function() {
    var countryHasLoaded = Boolean(this.country);
    var header = countryHasLoaded && (
      React.createElement(Header, null, 
        React.createElement("ul", {className: "nav navbar-nav project"}, 
          React.createElement(Header.Item, null, 
            React.createElement(Link, {to: "country", query: {c: [this.props.params.countryId]}, className: "back"}, 
              "Back to ", this.country.properties.name, " projects"
            )
          )
        )
      )
    );

    return (
      React.createElement("div", null, 
        header, 
        React.createElement(PointLayer, {
          key: 'la-la-la', 
          getMap: this.props.getMap, 
          pin: this.locationPin, 
          popup: this.locationPopup, 
          geojson: this.project})
      )
    );
  }

});


module.exports = Project;
