'use strict';

var assign = require('object-assign');
var React = require('react/addons');
var Reflux = require('reflux');
var FilterActions = require('../actions/filterActions');
var FiltersViewActions = require('../actions/filtersViewActions');
var FiltersViewStore = require('../stores/filtersViewStore');
var CountryActions = require('../actions/countryActions');
var CountrySummaryStore = require('../stores/countrySummaryStore');
var NotYet = require('./notYet.jsx');
var SectorStore = require('../stores/sectorStore');
var StatusStore = require('../stores/statusStore');
var ApprovalYearStore = require('../stores/approvalYearStore');
var FilterStore = require('../stores/filterStore');


var filters = [
  {
    id: 'country',
    isPrimary: true,
    name: 'Country',
    glyphicon: 'globe'
  },
  {
    id: 'sectors',
    isPrimary: true,
    name: 'Sector',
    glyphicon: 'grain'
  },
  {
    id: 'status',
    isPrimary: true,
    name: 'Status',
    glyphicon: 'ok-circle'
  },
  {
    id: 'approvalYear',
    isPrimary: true,
    name: 'Approval Year',
    glyphicon: 'calendar'
  },
  {
    id: 'basemap',
    isPrimary: false,
    name: 'Basemap',
    glyphicon: 'globe',
  },
  {
    id: 'share',
    isPrimary: false,
    name: 'Share',
    glyphicon: 'link'
  }
];


var FilterNavButton = React.createClass({displayName: "FilterNavButton",
  toggleMe: function(e) {
    if (e) { e.preventDefault() };  // if this is coming from a click event
    if (this.props.open) {
      FiltersViewActions.close(this.props.id);
    } else {
      FiltersViewActions.open(this.props.id);
    }
  },

  render: function() {
    var buttonClasses = React.addons.classSet({  // creates a space-separated string containing each key whose Boolean(value) is true
      'open': this.props.open,
      'active': this.props.active,
      'nav-button': true,
      'nav-button-primary': this.props.isPrimary
    });
    return (
      React.createElement("li", {role: "presentation", className: buttonClasses}, 
        this.props.isPrimary ?
          React.createElement("a", {href: "#", onClick: this.toggleMe}, 
            React.createElement("span", {className: 'glyphicon glyphicon-' + this.props.glyphicon, "aria-hidden": "true"}), 
            this.props.name
          ) :
          React.createElement(NotYet, null, 
            React.createElement("span", {className: 'glyphicon glyphicon-' + this.props.glyphicon, "aria-hidden": "true"}), 
            this.props.name
          )
        
      )
    );
  }
});


var ListItemMixin = {
  render: function() {
    return (
      React.createElement("li", null, 
        React.createElement("input", {
          id: this.props.id, 
          className: "visuallyhidden", 
          type: "checkbox", 
          checked: this.props.checked, 
          onChange: this.toggle}), 
        React.createElement("label", {htmlFor: this.props.id}, this.props.text)
      )
    );
  },
};

var RadioListItem = React.createClass({displayName: "RadioListItem",
  mixins: [ListItemMixin],
  toggle: function(e) {
    e && e.preventDefault();
    if (this.props.checked) {
      FilterActions.clearFilter(this.props.name, this.props.text);
    } else {
      FilterActions.setFilter(this.props.name, this.props.text);
    }
  },
});


var CountryRadioListItem = React.createClass({displayName: "CountryRadioListItem",
  mixins: [ListItemMixin],
  toggle: function(e) {
    e && e.preventDefault();
    if (this.props.checked) {
      CountryActions.removeCountry(this.props.countryId);
    } else {
      CountryActions.addCountry(this.props.countryId);
    }
  }
});


var FilterBody = React.createClass({displayName: "FilterBody",
  componentDidMount: function() {
    CountryActions.load();
  },
  render: function() {
    if (this.props.id === 'country') {
      var countries = this.props.countries.features;
      var selectedCountries = this.props.query.c || [];
      return (
        React.createElement("div", null, 
          React.createElement("h3", null, this.props.name), 
          countries.length === 0 ?
            React.createElement("p", null, "Loading countries...") :
            React.createElement("ul", {className: "filter-list compact"}, 
              countries.map(function(country) {
                return React.createElement(CountryRadioListItem, {
                  key: country.id, 
                  id: 'country-choice-' + country.id, 
                  countryId: country.id, 
                  text: country.properties.name, 
                  name: this.props.id, 
                  checked: selectedCountries.indexOf(country.id) !== -1})
              }, this)
            )
          
        )
      );
    } else if (this.props.id === 'sectors') {
      return (
        React.createElement("div", null, 
          React.createElement("h3", null, this.props.name), 
          React.createElement("ul", {className: "filter-list"}, 
            this.props.sectors.map(function(sector, i) {
              return React.createElement(RadioListItem, {
                key: i, 
                id: 'sector-choice-' + i, 
                text: sector, 
                name: this.props.id, 
                checked: this.props.filter.sectors.indexOf(sector) > -1});
            }, this)
          )
        )
      );
    } else if (this.props.id === 'status') {
      return (
        React.createElement("div", null, 
          React.createElement("h3", null, this.props.name), 
          React.createElement("ul", {className: "filter-list"}, 
            this.props.status.map(function(stat, i) {
              return React.createElement(RadioListItem, {
                key: i, 
                id: 'status-choice-' + i, 
                text: stat, 
                name: this.props.id, 
                checked: this.props.filter.status.indexOf(stat) > -1});
            }, this)
          )
        )
      );
    } else if (this.props.id == 'approvalYear') {
      return (
        React.createElement("div", null, 
          React.createElement("h3", null, this.props.name), 
          React.createElement("ul", {className: "filter-list"}, 
            this.props.approvalYear.map(function(year, i) {
              return React.createElement(RadioListItem, {
                key: i, 
                id: 'approval-choice-' + i, 
                text: year, 
                name: this.props.id, 
                checked: this.props.filter.approvalYear.indexOf(year) > -1});
            }, this)
          )
        )
      );
    } else {
      console.warn('unexpected filter id', this.props.id);
    }
  },
});


module.exports = React.createClass({displayName: "exports",

  mixins: [
    Reflux.connect(FiltersViewStore, 'fv'),
    Reflux.connect(CountrySummaryStore, 'countries'),
    Reflux.connect(SectorStore, 'sectors'),
    Reflux.connect(StatusStore, 'status'),
    Reflux.connect(ApprovalYearStore, 'approvalYear'),
    Reflux.connect(FilterStore, 'filter'),
    React.addons.PureRenderMixin,
  ],

  componentDidUpdate: function() {
    if (this.props.notifyWidthChange) {
      var newWidth = this.refs.container.getDOMNode().clientWidth;
      this.props.notifyWidthChange(newWidth);
    }
  },

  render: function() {

    var buttons = filters.map(function(filter) {
      var isOpen = filter.id === this.state.fv.currentlyOpen;
      var isActive = (this.state.fv.applied.indexOf(filter.id) !== -1) ||
        (filter.id === 'country' &&
          this.props.query.c && this.props.query.c.length > 1);
      return (
        React.createElement(FilterNavButton, React.__spread({
          key: filter.id}, 
          filter, 
          {active: isActive, 
          open: isOpen}))
      );
    }, this);

    var openFilter;
    if (this.state.fv.currentlyOpen) {
      filters.forEach(function(filter) {
        if (filter.id === this.state.fv.currentlyOpen) {
          openFilter = (
            React.createElement("div", {className: "filters-body"}, 
              React.createElement(FilterBody, React.__spread({}, 
                filter, 
                {countries: this.state.countries, 
                sectors: this.state.sectors, 
                status: this.state.status, 
                approvalYear: this.state.approvalYear, 
                filter: this.state.filter, 
                query: this.props.query}))
            )
          );
        }
      }, this);
    }

    return (
      React.createElement("div", {className: "filters", ref: "container"}, 

        React.createElement("div", {className: "filters-nav"}, 
          React.createElement("h3", {className: "filters-title"}, 
            "Map Quick Filters"
          ), 

          React.createElement("ul", {className: "nav nav-stacked filters-nav-buttons"}, 
            buttons
          )
        ), 

        openFilter

      )
    );
  }

});
