'use strict';


var Reflux = require('reflux');
var FilterActions = require('../actions/filterActions');
var DGSet = require('../util').DGSet;


var FilterStore = Reflux.createStore({

  listenables: [FilterActions],

  init: function() {
    this.data = {
      sectors: new DGSet([]),
      status: new DGSet([]),
      approvalYear: new DGSet([]),
    };
  },

  onSetFilter: function(name, filterData) {
    this.setSomething(name, filterData);
  },

  onClearFilter: function(name, filterData) {
    this.clearSomething(name, filterData);
  },

  getData: function() {
    return this.data;
  },

  setSomething: function(what, toAdd) {
    this.data[what].add(toAdd);
    this.emit();
  },

  clearSomething: function(what, toRemove) {
    this.data[what].remove(toRemove);
    this.emit();
  },

  get: function() {
    var _this = this;
    return Object.keys(this.data).reduce(function(emittable, dataKey) {
      emittable[dataKey] = _this.data[dataKey].asArray();
      return emittable;
    }, {});
  },

  emit: function() {
    this.trigger(this.get());
  },

  getInitialState: function() {
    return this.get();
  },

});


module.exports = FilterStore;
